import { useEffect, useState } from "react"
import { Outlet } from "react-router-dom"
import "./index.css"
import { PersonCircle } from "react-bootstrap-icons";
import { SideMenu } from "../../components/SideMenu";
import { logout } from '../../store/user';
import { useDispatch } from 'react-redux';
import Axios from "../../api/Axios";
import store from "../../store";
import trackUserEvent from "../../helpers/trackUserEvent";

const axios = new Axios();

const MyAccount = () => {

    let currentUser = store.getState().user.currentUser

    const [data, setData] = useState({
        firstName: currentUser.firstName,
        lastName: currentUser.lastName,
        // username: "",
        email: currentUser.email
    });

    const [sideMenu, setSideMenu] = useState([
        {
            title: "ACCOUNT DETAILS",
            link: "/my-account/edit-account",
        },
        {
            title: "ADDRESSES",
            link: "/my-account/addresses",
        },
        {
            title: "ORDERS",
            link: "/my-account/orders",
        },
        // {
        //     title: "WISH LIST",
        //     link: "/my-account/wishlist",
        // },
        {
            title: "LOG OUT",
            link: "/"
        }
    ])

    const dispatch = useDispatch();

    const onMenuClick = (title) => {
        if (title == "LOG OUT") {
            localStorage.clear()
            sessionStorage.clear()
            dispatch(logout())
        }
    }

    useEffect(() => {
        trackUserEvent("My Account")
    },[])

    return (
        <div className="container-fluid mb-3" style={{ backgroundColor: "white" }}>
            <div className="my-account-header">
                <div>
                    <h1 className="uppercase myAccountTitle">MY ACCOUNT</h1>
                    <small className="uppercase">DASHBOARD</small>
                </div>
            </div>
            <div className="row my-account-container">
                <div className="col-lg-3 col-sm-12" style={{ borderRight: "2px solid #c5c5c5" }}>
                    <div className="profile-section">
                        <PersonCircle style={{ width: "90px", display: "inlineBlock", height: "80px", color: "#c5c5c5" }} />
                        {/* <span className="profile-name">{data.username}</span> */}
                    </div>
                    <div className="side-menu-section">
                        {sideMenu.map((e, index) => < SideMenu title={e.title} link={e.link} key={index} handleClick={onMenuClick} />)}
                    </div>
                </div>
                <div className="col-lg-9">
                    <div className="outlet-section">
                        <Outlet context={data} />
                    </div>
                </div>
            </div>
        </div>
    )
}

export default MyAccount
