import "./index.css"
import { Link } from "react-router-dom"
import { useState } from "react"

export const SideMenu = ({ title, link, handleClick }) => {

    const [hover, setHover] = useState(false)
    return (
        <Link to={link} style={{ textDecoration: 'none', color: '#7c7c7c' }}>
            <div className={`side-menu ${hover ? "hover" : ""}`} onMouseEnter={() => setHover(true)} onMouseLeave={() => setHover(false)} onClick={() => handleClick(title)} >
                <span className="menu-title">{title}</span>
            </div>
        </Link>
    )
}

